import helper from '../../common/helper.js';
export default {
	data() {
		return {
			userfoArray: '',
			AmountArray: '',
			Id: '',
			paymentid: "",
		};
	},
	onLoad() {
		this.userinfo();
		
	},

	methods: {
		open(e) {;
			this.Id = e.currentTarget.id
		},
		// 获取金额列表
		userinfo() {
			uni.request({
				url: helper.websiteUrl + 'withdrawals/getpaymentlist',
				method: 'GET',
				data: helper.postdata({
					"userid": helper.getstate().userid,
					"type": "1"
				}),
				success: res => {
					if (res.data.code == 200) {
						this.userfoArray = res.data.data.Member;
						this.AmountArray = res.data.data.baseinfo;
						this.paymentid = res.data.data.baseinfo.ID;
						if (this.userfoArray.alipay == null) {
							this.userfoArray.alipay ='请填写账号';			
						}
						if(this.userfoArray.truename == ''){
							this.userfoArray.truename ='请填写姓名';
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				}
			});
		},
		tixian() {
			if (this.userfoArray.alipay == "请填写账号") {
				uni.showToast({
					icon: 'none',
					title: '请填写账号！'
				});
			}else if (this.Id == '') {
				uni.showToast({
					icon: 'none',
					title: '请选择提现金额！'
				});
			} else {
				uni.request({
					url: helper.websiteUrl + '/withdrawals/savewithdrawals',
					method: 'GET',
					data: helper.postdata({
						"userid": helper.getstate().userid,
						"type": "1",
						"paymentid": this.Id,
					}),
					success: res => {
						if (res.data.code == 200) {
							helper.goout(res.data.info);
							uni.showToast({
								icon: 'none',
								title: res.data.info
							});
							this.userinfo()
						} else {
							helper.goout(res.data.info);
							uni.showToast({
								icon: 'none',
								title: res.data.info
							});
						}
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				});
			}
		}
	}

}
